import React from 'react';
import { FiX, FiMail, FiPhone, FiLinkedin } from 'react-icons/fi';

const NotificationCenter = () => {
  const notifications = [
    {
      id: 1,
      type: 'email',
      title: 'New Email Communication',
      message: 'Tech Solutions Inc. replied to your email',
      time: '5 min ago'
    },
    {
      id: 2,
      type: 'phone',
      title: 'Scheduled Call',
      message: 'Upcoming call with Digital Innovations',
      time: '1 hour ago'
    },
    {
      id: 3,
      type: 'linkedin',
      title: 'LinkedIn Message',
      message: 'New connection request from Global Systems',
      time: '2 hours ago'
    }
  ];

  const getIcon = (type) => {
    switch (type) {
      case 'email': return <FiMail className="w-5 h-5" />;
      case 'phone': return <FiPhone className="w-5 h-5" />;
      case 'linkedin': return <FiLinkedin className="w-5 h-5" />;
      default: return <FiMail className="w-5 h-5" />;
    }
  };

  return (
    <div className="absolute right-0 mt-2 w-80 bg-white rounded-lg shadow-lg z-50">
      <div className="p-4 border-b">
        <div className="flex items-center justify-between">
          <h3 className="text-lg font-medium">Notifications</h3>
          <span className="bg-blue-100 text-blue-800 text-xs font-medium px-2.5 py-0.5 rounded">
            {notifications.length} new
          </span>
        </div>
      </div>
      
      <div className="max-h-96 overflow-y-auto">
        {notifications.map(notification => (
          <div key={notification.id} className="p-4 border-b hover:bg-gray-50">
            <div className="flex items-start">
              <div className="flex-shrink-0">
                {getIcon(notification.type)}
              </div>
              <div className="ml-3 w-0 flex-1">
                <p className="text-sm font-medium text-gray-900">
                  {notification.title}
                </p>
                <p className="mt-1 text-sm text-gray-500">
                  {notification.message}
                </p>
                <p className="mt-1 text-xs text-gray-400">
                  {notification.time}
                </p>
              </div>
              <button className="ml-3">
                <FiX className="w-4 h-4 text-gray-400 hover:text-gray-600" />
              </button>
            </div>
          </div>
        ))}
      </div>
      
      <div className="p-4 text-center border-t">
        <button className="text-sm text-blue-600 hover:text-blue-800">
          View all notifications
        </button>
      </div>
    </div>
  );
};

export default NotificationCenter; 