import React, { useState, useEffect } from 'react';
import { FiMail, FiPhone, FiLinkedin, FiCalendar, FiTrendingUp } from 'react-icons/fi';
import { LoadingOverlay } from '../../components/common/LoadingState';

const Dashboard = () => {
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    // Simulate data loading
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  }, []);

  // Sample data
  const stats = [
    { id: 1, name: 'Total Companies', value: '24', icon: FiTrendingUp, change: '+12%', changeType: 'increase' },
    { id: 2, name: 'Due Communications', value: '8', icon: FiCalendar, change: '-2', changeType: 'decrease' },
    { id: 3, name: 'Completed Today', value: '15', icon: FiMail, change: '+5', changeType: 'increase' }
  ];

  const recentCommunications = [
    {
      id: 1,
      company: 'Tech Solutions Inc.',
      type: 'email',
      date: '2024-01-15',
      status: 'completed',
      notes: 'Discussed Q1 plans'
    },
    {
      id: 2,
      company: 'Digital Innovations',
      type: 'phone',
      date: '2024-01-14',
      status: 'completed',
      notes: 'Product demo scheduled'
    },
    {
      id: 3,
      company: 'Global Systems Ltd',
      type: 'linkedin',
      date: '2024-01-14',
      status: 'pending',
      notes: 'Follow-up required'
    }
  ];

  if (isLoading) {
    return <LoadingOverlay isLoading={true} />;
  }

  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-semibold mb-6">Dashboard Overview</h1>
      
      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {stats.map((stat) => (
          <div key={stat.id} className="bg-white p-6 rounded-lg shadow-sm">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">{stat.name}</p>
                <p className="mt-1 text-3xl font-semibold text-gray-900">{stat.value}</p>
              </div>
              <div className={`p-3 rounded-md ${
                stat.changeType === 'increase' ? 'bg-green-100' : 'bg-red-100'
              }`}>
                <stat.icon className={`w-6 h-6 ${
                  stat.changeType === 'increase' ? 'text-green-600' : 'text-red-600'
                }`} />
              </div>
            </div>
            <div className="mt-4">
              <span className={`text-sm font-medium ${
                stat.changeType === 'increase' ? 'text-green-600' : 'text-red-600'
              }`}>
                {stat.change}
              </span>
              <span className="text-sm text-gray-600"> from last month</span>
            </div>
          </div>
        ))}
      </div>

      {/* Recent Communications */}
      <div className="bg-white rounded-lg shadow-sm">
        <div className="p-6">
          <h2 className="text-lg font-medium text-gray-900 mb-4">Recent Communications</h2>
          <div className="space-y-4">
            {recentCommunications.map((comm) => (
              <div key={comm.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center space-x-4">
                  <div className={`p-2 rounded-md ${
                    comm.status === 'completed' ? 'bg-green-100' : 'bg-yellow-100'
                  }`}>
                    {comm.type === 'email' && <FiMail className="w-5 h-5" />}
                    {comm.type === 'phone' && <FiPhone className="w-5 h-5" />}
                    {comm.type === 'linkedin' && <FiLinkedin className="w-5 h-5" />}
                  </div>
                  <div>
                    <p className="font-medium text-gray-900">{comm.company}</p>
                    <p className="text-sm text-gray-600">{comm.notes}</p>
                  </div>
                </div>
                <div className="text-sm text-gray-600">{comm.date}</div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard; 