import React, { useState } from 'react';
import { Calendar, dateFnsLocalizer } from 'react-big-calendar';
import format from 'date-fns/format';
import parse from 'date-fns/parse';
import startOfWeek from 'date-fns/startOfWeek';
import getDay from 'date-fns/getDay';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import { FiPlus, FiEdit2, FiTrash2 } from 'react-icons/fi';

const locales = {
  'en-US': require('date-fns/locale/en-US')
};

const localizer = dateFnsLocalizer({
  format,
  parse,
  startOfWeek,
  getDay,
  locales
});

const CommunicationCalendar = () => {
  const [events, setEvents] = useState([
    {
      id: 1,
      title: 'Email - Tech Solutions Inc.',
      start: new Date(2024, 0, 15, 10, 0),
      end: new Date(2024, 0, 15, 11, 0),
      type: 'email',
      company: 'Tech Solutions Inc.',
      notes: 'Follow up on proposal'
    }
  ]);

  const [selectedEvent, setSelectedEvent] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const handleSelect = ({ start, end }) => {
    setSelectedEvent({ start, end });
    setIsModalOpen(true);
  };

  const handleEventSelect = (event) => {
    setSelectedEvent(event);
    setIsModalOpen(true);
  };

  const handleSave = async (formData) => {
    setIsLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      if (selectedEvent.id) {
        // Update existing event
        setEvents(events.map(event => 
          event.id === selectedEvent.id 
            ? { ...event, ...formData }
            : event
        ));
      } else {
        // Add new event
        setEvents([...events, {
          id: Date.now(),
          ...formData,
          title: `${formData.type} - ${formData.company}`
        }]);
      }
      setIsModalOpen(false);
    } catch (error) {
      console.error('Error saving event:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleDelete = async (id) => {
    setIsLoading(true);
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1000));
      setEvents(events.filter(event => event.id !== id));
    } catch (error) {
      console.error('Error deleting event:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const eventStyleGetter = (event) => {
    let backgroundColor = '#3182ce'; // default blue
    switch (event.type) {
      case 'email':
        backgroundColor = '#38a169'; // green
        break;
      case 'phone':
        backgroundColor = '#d69e2e'; // yellow
        break;
      case 'linkedin':
        backgroundColor = '#0077b5'; // LinkedIn blue
        break;
      default:
        break;
    }
    return { style: { backgroundColor } };
  };

  return (
    <div className="h-screen p-6">
      {isLoading && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-white"></div>
        </div>
      )}

      <div className="mb-4 flex justify-between items-center">
        <h1 className="text-2xl font-semibold text-gray-900">Communication Calendar</h1>
        <button
          onClick={() => {
            setSelectedEvent({ start: new Date(), end: new Date() });
            setIsModalOpen(true);
          }}
          className="btn-primary"
        >
          <FiPlus className="mr-2" />
          Add Communication
        </button>
      </div>

      <Calendar
        localizer={localizer}
        events={events}
        startAccessor="start"
        endAccessor="end"
        style={{ height: 'calc(100vh - 200px)' }}
        onSelectSlot={handleSelect}
        onSelectEvent={handleEventSelect}
        selectable
        eventPropGetter={eventStyleGetter}
        views={['month', 'week', 'day']}
      />

      {/* Event Modal */}
      {isModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-40">
          <div className="bg-white rounded-lg p-6 w-full max-w-md">
            <h2 className="text-xl font-semibold mb-4">
              {selectedEvent.id ? 'Edit Communication' : 'New Communication'}
            </h2>
            <form onSubmit={(e) => {
              e.preventDefault();
              const formData = new FormData(e.target);
              handleSave({
                start: selectedEvent.start,
                end: selectedEvent.end,
                type: formData.get('type'),
                company: formData.get('company'),
                notes: formData.get('notes')
              });
            }}>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Type
                  </label>
                  <select
                    name="type"
                    defaultValue={selectedEvent.type}
                    className="form-input"
                    required
                  >
                    <option value="email">Email</option>
                    <option value="phone">Phone Call</option>
                    <option value="linkedin">LinkedIn</option>
                    <option value="other">Other</option>
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Company
                  </label>
                  <input
                    type="text"
                    name="company"
                    defaultValue={selectedEvent.company}
                    className="form-input"
                    required
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Notes
                  </label>
                  <textarea
                    name="notes"
                    defaultValue={selectedEvent.notes}
                    className="form-input"
                    rows={3}
                  />
                </div>

                <div className="flex justify-end space-x-3">
                  {selectedEvent.id && (
                    <button
                      type="button"
                      onClick={() => handleDelete(selectedEvent.id)}
                      className="btn-secondary text-red-600"
                    >
                      <FiTrash2 className="mr-2" />
                      Delete
                    </button>
                  )}
                  <button
                    type="button"
                    onClick={() => setIsModalOpen(false)}
                    className="btn-secondary"
                  >
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="btn-primary"
                    disabled={isLoading}
                  >
                    {isLoading ? 'Saving...' : 'Save'}
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default CommunicationCalendar; 