// App.js
import React from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import ErrorBoundary from './components/common/ErrorBoundary';

// Layout Components
import DashboardLayout from './components/layout/DashboardLayout';

// Auth Pages
import Login from './pages/auth/Login';
import SignUp from './pages/auth/SignUp';

// Admin Pages
import CompanyManagement from './pages/admin/CompanyManagement';
import CommunicationMethodManagement from './pages/admin/CommunicationMethodManagement';

// User Pages
import Dashboard from './pages/user/Dashboard';
import CommunicationCalendar from './pages/calendar/CommunicationCalendar';
import AnalyticsDashboard from './pages/analytics/AnalyticsDashboard';
import ProfileSettings from './pages/user/ProfileSettings';

// Auth Utils
const isAuthenticated = () => localStorage.getItem('auth-token') !== null;
const isAdmin = () => localStorage.getItem('user-role') === 'admin';

// Protected Route Component
const ProtectedRoute = ({ children, adminOnly = false }) => {
  if (!isAuthenticated()) {
    return <Navigate to="/login" />;
  }

  if (adminOnly && !isAdmin()) {
    return <Navigate to="/dashboard" />;
  }

  return children;
};

function App() {
  return (
    <ErrorBoundary>
      <Router basename="/">
        <Routes>
          {/* Public Routes */}
          <Route path="/login" element={
            !isAuthenticated() ? <Login /> : <Navigate to="/dashboard" />
          } />
          <Route path="/signup" element={
            !isAuthenticated() ? <SignUp /> : <Navigate to="/dashboard" />
          } />

          {/* Protected Routes */}
          <Route path="/" element={
            <ProtectedRoute>
              <DashboardLayout />
            </ProtectedRoute>
          }>
            <Route index element={<Navigate to="/dashboard" replace />} />
            <Route path="dashboard" element={<Dashboard />} />
            <Route path="calendar" element={<CommunicationCalendar />} />
            <Route path="analytics" element={<AnalyticsDashboard />} />

            {/* Admin Routes */}
            <Route path="admin/companies" element={
              <ProtectedRoute adminOnly>
                <CompanyManagement />
              </ProtectedRoute>
            } />
            <Route path="admin/communication-methods" element={
              <ProtectedRoute adminOnly>
                <CommunicationMethodManagement />
              </ProtectedRoute>
            } />

            <Route path="/profile" element={<ProfileSettings />} />
          </Route>

          {/* Catch all route */}
          <Route path="*" element={<Navigate to="/dashboard" />} />
        </Routes>
      </Router>
    </ErrorBoundary>
  );
}

export default App;
