import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { FiMail, FiLock, FiUser, FiAlertCircle } from 'react-icons/fi';

const Login = () => {
  const navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  const DEMO_CREDENTIALS = {
    admin: { email: 'admin@calcomm.com', password: 'admin123' },
    user: { email: 'user@calcomm.com', password: 'user123' }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError('');

    const email = e.target.email.value;
    const password = e.target.password.value;

    try {
      if ((email === DEMO_CREDENTIALS.admin.email && password === DEMO_CREDENTIALS.admin.password) ||
          (email === DEMO_CREDENTIALS.user.email && password === DEMO_CREDENTIALS.user.password)) {
        
        const role = email === DEMO_CREDENTIALS.admin.email ? 'admin' : 'user';
        localStorage.setItem('auth-token', 'demo-token');
        localStorage.setItem('user-role', role);
        localStorage.setItem('user-email', email);
        
        navigate('/dashboard');
      } else {
        setError('Invalid credentials. Please use the demo credentials below.');
      }
    } catch (err) {
      setError('Login failed. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen flex">
      {/* Left Side - Form */}
      <div className="flex-1 flex flex-col justify-center px-4 sm:px-6 lg:px-20 xl:px-24">
        <div className="mx-auto w-full max-w-sm lg:w-96">
          <div className="text-center">
            <h1 className="text-3xl font-bold text-blue-600">CalComm</h1>
            <h2 className="mt-6 text-3xl font-extrabold text-gray-900">Welcome back</h2>
            <p className="mt-2 text-sm text-gray-600">
              Don't have an account?{' '}
              <Link to="/signup" className="font-medium text-blue-600 hover:text-blue-500">
                Sign up
              </Link>
            </p>
          </div>

          {error && (
            <div className="mt-4 bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded-lg flex items-center">
              <FiAlertCircle className="w-5 h-5 mr-2" />
              {error}
            </div>
          )}

          <form className="mt-8 space-y-6" onSubmit={handleSubmit}>
            <div className="space-y-4">
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                  Email address
                </label>
                <div className="mt-1 relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <FiMail className="h-5 w-5 text-gray-400" />
                  </div>
                  <input
                    id="email"
                    name="email"
                    type="email"
                    required
                    className="pl-10 block w-full border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter your email"
                  />
                </div>
              </div>

              <div>
                <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                  Password
                </label>
                <div className="mt-1 relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <FiLock className="h-5 w-5 text-gray-400" />
                  </div>
                  <input
                    id="password"
                    name="password"
                    type="password"
                    required
                    className="pl-10 block w-full border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                    placeholder="Enter your password"
                  />
                </div>
              </div>
            </div>

            <div>
              <button
                type="submit"
                disabled={isLoading}
                className={`w-full flex justify-center py-3 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 ${
                  isLoading ? 'opacity-75 cursor-not-allowed' : ''
                }`}
              >
                {isLoading ? 'Signing in...' : 'Sign in'}
              </button>
            </div>
          </form>

          {/* Demo Credentials */}
          <div className="mt-8 bg-blue-50 border border-blue-100 rounded-lg p-4">
            <h3 className="text-sm font-medium text-blue-800 mb-3">Demo Credentials</h3>
            <div className="space-y-3 text-sm">
              <div className="p-3 bg-white rounded-md border border-blue-100">
                <p className="font-semibold text-gray-700">Admin Access</p>
                <p className="text-gray-600">Email: admin@calcomm.com</p>
                <p className="text-gray-600">Password: admin123</p>
              </div>
              <div className="p-3 bg-white rounded-md border border-blue-100">
                <p className="font-semibold text-gray-700">User Access</p>
                <p className="text-gray-600">Email: user@calcomm.com</p>
                <p className="text-gray-600">Password: user123</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Right Side - Image/Info */}
      <div className="hidden lg:block relative w-0 flex-1">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-600 to-blue-800">
          <div className="absolute inset-0 bg-opacity-75">
            <div className="flex h-full items-center justify-center p-12">
              <div className="max-w-xl text-white">
                <h2 className="text-4xl font-bold mb-6">
                  Manage Your Business Communications Effectively
                </h2>
                <ul className="space-y-4">
                  <li className="flex items-center">
                    <FiUser className="h-6 w-6 mr-4" />
                    <span>Track all your business communications in one place</span>
                  </li>
                  <li className="flex items-center">
                    <FiMail className="h-6 w-6 mr-4" />
                    <span>Automated email, phone, and LinkedIn tracking</span>
                  </li>
                  <li className="flex items-center">
                    <FiLock className="h-6 w-6 mr-4" />
                    <span>Secure and reliable communication management</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login; 