import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { 
  FiHome, 
  FiCalendar, 
  FiPieChart, 
  FiUsers, 
  FiStar, 
  FiSettings,
  FiBarChart2 
} from 'react-icons/fi';

const Sidebar = () => {
  const location = useLocation();
  const isAdmin = localStorage.getItem('user-role') === 'admin';

  const menuItems = [
    { path: '/dashboard', icon: FiHome, label: 'Overview' },
    { path: '/calendar', icon: FiCalendar, label: 'Calendar' },
    { path: '/analytics', icon: FiBarChart2, label: 'Analytics' },
    ...(isAdmin ? [{ path: '/admin/companies', icon: FiUsers, label: 'Companies' }] : []),
  ];

  const favoriteCompanies = [
    { id: 1, name: 'Tech Solutions Inc.' },
    { id: 2, name: 'Digital Innovations' },
    { id: 3, name: 'Global Systems Ltd' }
  ];

  return (
    <div className="fixed left-0 top-0 h-full w-64 bg-white shadow-lg">
      <div className="p-6">
        <h1 className="text-2xl font-bold text-blue-600">CalComm</h1>
      </div>

      <nav className="mt-6">
        {menuItems.map((item) => (
          <Link
            key={item.path}
            to={item.path}
            className={`flex items-center px-6 py-3 text-gray-700 hover:bg-gray-100 ${
              location.pathname === item.path ? 'bg-blue-50 text-blue-600' : ''
            }`}
          >
            <item.icon className="w-5 h-5 mr-3" />
            <span>{item.label}</span>
          </Link>
        ))}
      </nav>

      <div className="mt-8 px-6">
        <h2 className="text-xs font-semibold text-gray-600 uppercase tracking-wider">
          FAVORITES
        </h2>
        <div className="mt-3 space-y-2">
          {favoriteCompanies.map(company => (
            <Link
              key={company.id}
              to={`/companies/${company.id}`}
              className="block px-3 py-2 text-sm text-gray-700 hover:bg-gray-100 rounded-md"
            >
              {company.name}
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Sidebar; 